
import { IAbility, IEffect, IEquipment, 
    AbilityType 
} from './index';
export enum CreatureState { Alive, Dead, DeadInThisTurn };
export interface ICreature {
    name: string;
    //image: ImageType;
    description: string;
    img: string;
    hitPoint: number;
    maxHitPoint: number;
    state: CreatureState;
    abilities: AbilityType[];          //  
    currentAbilities: AbilityType[];   //  ,    
    // !!!     1   
    usedInThisBattleAbilities: number[];       //  ,     <AbilityType, number>
    usedInThisRoundAbilities: AbilityType[];   //  ,    
    // !!!       .     -    :
    effects: IEffect[];                 //   . 
    currentEffects: IEffect[];          //      
    equipment: IEquipment;    
    lastDiceDamage: number;
    lastDiceTarget: number;
}


